.sources-grid {
  margin: 30px 30px; }
  .sources-grid ._category {
    border: 3px solid transparent;
    cursor: pointer;
    -webkit-transition: color 0.25s, border 0.25s, background-color 1s, -webkit-box-shadow 0.5s;
    transition: color 0.25s, border 0.25s, background-color 1s, -webkit-box-shadow 0.5s;
    -o-transition: color 0.25s, border 0.25s, background-color 1s, box-shadow 0.5s;
    transition: color 0.25s, border 0.25s, background-color 1s, box-shadow 0.5s;
    transition: color 0.25s, border 0.25s, background-color 1s, box-shadow 0.5s, -webkit-box-shadow 0.5s;
