.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }

.darken-gradient {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.05)), to(rgba(0, 0, 0, 0.65)));
  background: -webkit-linear-gradient(rgba(255, 255, 255, 0.05), rgba(0, 0, 0, 0.65));
  background: -o-linear-gradient(rgba(255, 255, 255, 0.05), rgba(0, 0, 0, 0.65));
  background: linear-gradient(rgba(255, 255, 255, 0.05), rgba(0, 0, 0, 0.65)); }

@-webkit-keyframes pulse {
  5% {
    opacity: 1; }
  50% {
    opacity: 0; }
  95% {
    opacity: 1; } }

@keyframes pulse {
  5% {
    opacity: 1; }
  50% {
    opacity: 0; }
  95% {
    opacity: 1; } }



